<template>
  <div id="ranking-board">
    <div class="ranking-board-title">进程列表</div>
    <dv-scroll-ranking-board :config="config" />
  </div>
</template>

<script>
export default {
  name: 'RankingBoard',
  data () {
    return {
      config: {
        data: [
          {
            name: 'java',
            value: 55
          },
          {
            name: 'top',
            value: 120
          },
          {
            name: 'ps',
            value: 78
          },
          {
            name: 'rm -f',
            value: 66
          },
          {
            name: 'python',
            value: 80
          },
          {
            name: 'df -h',
            value: 45
          },
          {
            name: 'ojbk',
            value: 29
          },
          {
            name: 'md',
            value: 29
          },
          {
            name: 'cd',
            value: 29
          },
          {
            name: 'kill -9',
            value: 32
          }
        ],
        rowNum: 10
      }
    }
  },
  props: {
    // 树莓派信息
    raspiInfo: {
      type: Object
    }
  },
  /**
   * 监听
   */
  watch: {
    raspiInfo: {
      // 深度监听，可监听到对象、数组的变化
      handler (val, oldVal) {
        // console.log('raspiInfo', val)
        this.loadData(val)
      },
      // true 深度监听
      deep: true
    }
  },
  methods: {
    /**
     * 加载数据
     * @param raspiInfo
     */
    loadData (raspiInfo) {
      if (raspiInfo) {
        let taskList = raspiInfo.taskList
        let data = []
        let config = {}
        for (let i = 0; i < taskList.length; i++) {
          let task = taskList[i]
          data.push({
            name: task.command,
            value: task.res
          })
        }
        config.data = data
        config.rowNum = taskList.length
        this.config = config
      }
    }
  }
}
</script>

<style lang="less">
#ranking-board {
  width: 20%;
  box-shadow: 0 0 3px blue;
  display: flex;
  flex-direction: column;
  background-color: rgba(6, 30, 93, 0.5);
  border-top: 2px solid rgba(1, 153, 209, .5);
  box-sizing: border-box;
  padding: 0px 30px;

  .ranking-board-title {
    font-weight: bold;
    height: 50px;
    display: flex;
    align-items: center;
    font-size: 20px;
  }

  .dv-scroll-ranking-board {
    flex: 1;
  }
}
</style>
